import React, { useState, useEffect } from "react";
// import { Route, Switch } from "react-router-dom";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";

import { Row, Col, Card, Drawer, Button, Dropdown, Menu, Spin } from "antd";
import PageTitle from "../../../layout/components/content/page-title";
import "./reports.less";

import { Link } from "react-router-dom";

import { getReportsMenuItems } from "../../../store/system/systemSlice";

function Reports(props) {
  useEffect(() => {
    props.getReportsMenuItems();
  }, []);

  return (
    <Row gutter={[12, 12]} >
      <PageTitle pageTitle="Reports" />
      <Col span={24}>
        <div className="Reports-dashboard">
          <div className="Reports-dashboard-menu">
            <Row>
              {/* props.getModelName(report); */}
              {props.reportsMenuItemsList?.menuitems &&
                props.reportsMenuItemsList?.menuitems.map((report, index) => (
                  <React.Fragment key={index}>
                    <Col xl={8} xxl={6} md={12} span={24}>
                      <Link
                        to={report.link}
                        className="waves-effect ml-2 hp-pt-12"
                      >
                        <div className="Reports-dashboard-item hp-m-12 ">
                          <div className="title-box hp-d-inline-block ">
                            <span className="mt-4">{report.title}</span>
                          </div>

                          <div className="view-box hp-d-inline-block hp-float-right">
                            <Link
                              to={report.link}
                              className="waves-effect ml-2 hp-pt-12"
                            >
                              <i className="ri-menu-fill"></i>
                            </Link>
                          </div>
                        </div>
                      </Link>
                    </Col>
                  </React.Fragment>
                ))}
            </Row>
          </div>
        </div>
      </Col>
    </Row>
  );
}
function mapStatetoProps({ system, reports }) {
  return {
    reportsMenuItemsList: system.reportsMenuItemsList,
    reportsItemsList: reports.reportsItemsList,
    loading: reports.loading,
  };
}

export default withRouter(
  connect(mapStatetoProps, {
    getReportsMenuItems,
  })(Reports)
);
